	//* Páginas:
	$pg-maxwidth: 1900px;
	$pg-hdm-zoom: 120%;

	//* Tamanho do REM:
	$s-rem: 16px;

//? CONFIGURAÇÃO DO FRAMEWORK:
//? --------------------------------------------------------------------------------------------------------//
	//* Zoom:
	@include mix.responsive(1500px, min) {
		html.hdm-zoom {
			zoom: $pg-hdm-zoom;
		}
	}

	//* Inativador: [https://webeponto.notion.site/Inativadores-946fec746aa74cf28b5e8844fe1e5bff]
	.hidden {
		display: none !important;
	}

	@include mix.responsive() {
		.mob\:hidden {
			display: none !important;
		}
	}

	//* Cursores: [https://webeponto.notion.site/Cursores-12f605114bde45e1a55af00954299f65]
	.mc {
		&-auto {
			cursor: auto;
		}

		&-pointer {
			cursor: pointer;
		}

		&-help {
			cursor: context-menu;
		}

		&-grab {
			cursor: grab;
		}

		&-grabbed {
			cursor: grabbing;
		}

		&-text {
			cursor: text;
		}

		&-zoom-in {
			cursor: zoom-in;
		}

		&-zoom-out {
			cursor: zoom-out;
		}

		&-resize {
			cursor: nwse-resize;
		}
	}

	//* Posicionamento: [https://webeponto.notion.site/Posicionamento-d96cf6193d484249979bd186b767b7c6]
	$positions: static, sticky, relative, absolute, fixed;

	@each $position in $positions {
		.#{$position} {
			position: $position !important;
		}
	}

	@include mix.responsive() {
		.mob\: {
			&static {
				position: static !important;
			}
			
			&sticky {
				position: sticky !important;
			}
	
			&relative {
				position: relative !important;
			}

			&absolute {
				position: absolute !important;
			}

			&fixed {
				position: fixed !important;
			}
		}
	}

	//* Menu de Contexto: [https://webeponto.notion.site/Popover-Menu-Contextual-8e86b746525446e382a24b9826bdef85]
	[popover]{
		top: anchor(top);
		left: anchor(right);
		position-try-options: flip-block, flip-inline;

		// Ancoramento:
		&.y {
			&-top{
				top: anchor(top);
			}
			
			&-bottom{
				top: anchor(bottom);
			}
		}
		
		&.x {
			&-left{
				left: anchor(left);
			}
			
			&-right{
				left: anchor(right);
			}
		}

		&.inset{
			// Top:
			&-top{
				inset-area: top;
			}
			
			&-top\/left{
				inset-area: top span-left;
			}
			
			&-top\/right{
				inset-area: top span-right;
			}
			
			&-top\/all,
			&-top\/full{
				inset-area: top span-all;
			}

			// Left:
			&-left{
				inset-area: left;
			}

			&-left\/top{
				inset-area: left span-top;
			}
			
			&-left\/bottom{
				inset-area: left span-bottom;
			}

			&-left\/all,
			&-left\/full{
				inset-area: left span-all;
			}

			// Bottom:
			&-bottom{
				inset-area: bottom;
			}
			
			&-bottom\/left{
				inset-area: bottom span-left;
			}
			
			&-bottom\/right{
				inset-area: bottom span-right;
			}
			
			&-bottom\/all,
			&-bottom\/full{
				inset-area: bottom span-all;
			}

			// Right:
			&-right{
				inset-area: right;
			}

			&-right\/top{
				inset-area: right span-top;
			}
			
			&-right\/bottom{
				inset-area: right span-bottom;
			}

			&-right\/all,
			&-right\/full{
				inset-area: right span-all;
			}

			// Corners:
			&-top-left{
				inset-area: top left;
			}
			
			&-top-right{
				inset-area: top right;
			}
			
			&-bottom-left{
				inset-area: bottom left;
			}

			&-bottom-right{
				inset-area: bottom right;
			}
		}

		// Especificação de tamanho:
		&.w {
			@for $w from 1 through 18 {
				$v: $w * 5;

				&#{$v},
				&-#{$v} {
					&-min {
						min-inline-size: #{$v * 1%};
					}

					&-max {
						max-inline-size: #{$v * 1%};
					}
				}
			}

			&1\/3,
			&-33 {
				&-min {
					min-inline-size: 33%;
				}

				&-max {
					max-inline-size: 33%;
				}
			}

			&2\/3,
			&-66 {
				&-min {
					min-inline-size: 66%;
				}

				&-max {
					max-inline-size: 66%;
				}
			}

			@for $w from 1 through 20 {
				&#{$w}r,
				&-#{$w}r {
					&-min {
						min-inline-size: #{$w * 1rem};
					}

					&-max {
						max-inline-size: #{$w * 1rem};
					}
				}
			}
			
			@for $w from 1 through 20 {
				&#{$w}em,
				&-#{$w}em {
					&-min {
						min-inline-size: #{$w * 1em};
					}

					&-max {
						max-inline-size: #{$w * 1em};
					}
				}
			}

			@for $w from 1 through 60 {
				$v: $w * 5;
				
				&#{$v}px,
				&-#{$v}px {
					&-min {
						min-inline-size: #{$v * 1px};
					}

					&-max {
						max-inline-size: #{$v * 1px};
					}
				}
			}

			&-full,
			&100,
			&-100 {
				&-min {
					min-inline-size: 100%;
				}

				&-max {
					max-inline-size: 100%;
				}
			}
			
			&-clear{
				&-min {
					min-inline-size: unset;
				}

				&-max {
					max-inline-size: unset;
				}
			}
		}
	}

