	//* Distanciamento de Linhas de Texto:
	.lh{
		@for $lh from 0 through 8 {
			$v: $lh * 10%;

			&-#{$lh} {
				line-height: #{$v + 100%};
			}
		}

		@for $lh from 0 through 8 {
			$v: math.div($lh, 10) * 1em;

			&-#{$lh}em {
				line-height: #{$v + 1em};
			}
		}
	}

