					&.mob\:gap{
						@for $g from 0 through 5 {
							&-#{$g} {
								border-spacing: #{$g * 1%};
							}
						}

						@for $g from 0 through 100 {
							&-#{$g}px {
								border-spacing: #{$g * 1px};
							}
						}

						@for $g from 0 through 15 {
							&-#{$g}r {
								border-spacing: #{$g * 1rem};
							}
						}
						
						@for $g from 0 through 15 {
							&-#{$g}em {
								border-spacing: #{$g * 1em};
							}
						}
					}

					// Legenda/título da tabela:
					&.mob\:title{
						&-first{
							caption-side: top;
						}
						
						&-last{
							caption-side: bottom;
						}
					}
					
					// Algoritmo de dimensionamento da coluna:
					&.mob\:col{
