	//* Transições:
	.transition {
		transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
		transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
		transition-duration: 150ms;

		//? Tipos de transição:
		&-none {
			transition-property: none;
		}

		&-all {
			transition-property: all;
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
			transition-duration: 150ms;
		}

		&-colors {
			transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
			transition-duration: 150ms;
		}

		&-opacity {
			transition-property: opacity;
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
			transition-duration: 150ms;
		}

		&-shadow {
			transition-property: box-shadow;
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
			transition-duration: 150ms;
		}

		&-transform {
			transition-property: transform;
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
			transition-duration: 150ms;
		}
	}

	// Durações e atrasos da transição
	.duration {
		@for $d from 0 through 21 {
			$v: $d * 50;

			&-#{$v} {
				transition-duration: #{$v}ms;
			}
		}
	}

	.delay {
		@for $d from 0 through 21 {
			$v: $d * 50;

			&-#{$v} {
				transition-duration: #{$v}ms;
			}
		}
	}

	// Função de tempo da transição
	.ease {
		&-linear {
			transition-timing-function: linear;
		}

		&-in {
			transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
		}

		&-out {
			transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
		}

		&-in-out {
			transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
		}
	}

	//* Escalas:
	.scale {
		@for $s from 0 through 15 {
			$cn: $s * 10;
			$v: calc($cn / 100);

			&-#{$cn} {
				transform: scale(#{$v});

				&-neg {
					transform: scale(-#{$v});
				}
			}

		}
	}

	//* Opacidade:
	.opacity {
		@for $o from 0 through 10 {
			$cn: $o * 10;
			$v: calc($cn / 100);

			&-#{$cn} {
				opacity: #{$v};
			}
		}
	}

	//* Rotações:
	.rotate {
		@for $r from 0 through 360 {
			&-#{$r} {
				transform: rotate(#{$r});

				&-neg {
					transform: rotate(-#{$r});
				}
			}

		}
	}

	//* Translações:
	.translate {
		@for $t from 0 through 20 {
			$v: $t * 5;

			&-x-#{$v} {
				transform: translateX(#{$v * 1%});

				&-neg {
					transform: translateX(-#{$v * 1%});
				}
			}

			&-y-#{$v} {
				transform: translateY(#{$v * 1%});

				&-neg {
					transform: translateY(-#{$v * 1%});
				}
			}
		}

		&-x-1\/3,
		&-x-33 {
			transform: translateX(33%);

			&-neg {
				transform: translateX(-33%);
			}
		}

		&-y-1\/3,
		&-y-33 {
			transform: translateY(33%);

			&-neg {
				transform: translateY(-33%);
			}
		}

		&-x-2\/3,
		&-x-66 {
			transform: translateX(66%);

			&-neg {
				transform: translateX(-66%);
			}
		}

		&-y-2\/3,
		&-y-66 {
			transform: translateY(66%);

			&-neg {
				transform: translateY(-66%);
			}
		}

		&-x-1\/2 {
			transform: translateX(50%);

			&-neg {
				transform: translateX(-50%);
			}
		}

		&-y-1\/2 {
			transform: translateY(50%);

			&-neg {
				transform: translateY(-50%);
			}
		}

		&-x-100,
		&-x-full {
			transform: translateX(100%);

			&-neg {
				transform: translateX(-100%);
			}
		}

		&-y-100,
		&-y-full {
			transform: translateY(100%);

			&-neg {
				transform: translateY(-100%);
			}
		}

		@for $t from 0 through 20 {
			&-x-#{$t}r {
				transform: translateX(#{$t * 1rem});

				&-neg {
					transform: translateX(-#{$t * 1rem});
				}
			}

			&-y-#{$t}r {
				transform: translateY(#{$t * 1rem});

				&-neg {
					transform: translateY(-#{$t * 1rem});
				}
			}
		}

		@for $t from 0 through 20 {
			&-x-#{$t}px {
				transform: translateX(#{$t * 1px});

				&-neg {
					transform: translateX(-#{$t * 1px});
				}
			}

			&-y-#{$t}px {
				transform: translateY(#{$t * 1px});

				&-neg {
					transform: translateY(-#{$t * 1px});
				}
			}
		}

	}

